import { Component, OnInit } from '@angular/core';
import { Parser, HtmlRenderer } from 'commonmark';
import { Post, BlogService } from '../blog.service';
import { ActivatedRoute, Router } from '@angular/router';


@Component({
  selector: 'app-preview',
  templateUrl: './preview.component.html',
  styleUrls: ['./preview.component.css']
})
export class PreviewComponent implements OnInit {

  private post: Post;
  private previewTitle;
  private previewBody;
  private postID: number;
  
  constructor(private activatedRoute: ActivatedRoute, private blogService: BlogService,
    private router: Router) { 
      activatedRoute.params.subscribe((paramas)=>this.postID = (paramas['id']));
      this.post = this.blogService.getPost(Number(this.postID));
    }

  ngOnInit() {
    this.activatedRoute.params.subscribe(() => this.renderPreview());
  }

  renderPreview(): void {
    let reader = new Parser;
    let writer = new HtmlRenderer;
    if(this.post != null) {
      this.previewTitle = writer.render(reader.parse(this.post.title));
      this.previewBody = writer.render(reader.parse(this.post.body));
    }
  }

  editPage(): void {
    this.router.navigate(['/edit/' + this.post.postid]);
  }
}
